import MaterialTable from "@material-table/core"
import moment from "moment"
import { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import Swal from "sweetalert2"
import { tramiteSetActive, tramiteStartDelete, tramiteStartUpdate } from "../../../../actions/tramites"
import { uiOpenTramiteModal } from "../../../../actions/ui"
import { IRootState } from "../../../../interfaces/interfaces"
import { TramiteList } from "../../../../types/types"

const columns = [
    {title:'Fecha',field:'date'},
    {title:'Descripcion',field:'description'},
    {title:'Monto',field:'amount'},
    {title:'Seña', field:'prepay'},
    {title:'Nombre',field:'name'},
    {title:'Telefono',field:'phone'},
    {title:'Patente', field:'plate'},
    {title:'Estado' ,field:'isComplete', render: (rowData:any) => rowData.isComplete ? (<h4 style={{color:'#90ee90'}}>Listo</h4>) : (<h4 style={{color:'#f08080'}}>En proceso...</h4>)}
]

const List = () => {

    const dispatch = useDispatch()

    const {tramites} = useSelector((state:IRootState) => state.tramite)
    const [tramiteParsed, setTramiteParsed] = useState<TramiteList[]>([])
    
    useEffect(() => {
        const parseTramite:TramiteList[] = []
        tramites.forEach((t) => {
            const tramite:TramiteList = {
                id: t.id,
                date: moment(t.date).format('DD-MM-YYYY').toString(),
                plate: t.plate,
                description: t.description,
                amount: t.amount,
                prepay: t.prepay,
                name: t.name,
                phone: t.phone,
                isComplete: t.isComplete
            }
            parseTramite.push(tramite)
        })
        setTramiteParsed(parseTramite)
    }, [tramites])
    const handleEdit = (tramite:any) => {
        console.log(tramite)
        const tramiteFull = tramites.filter(e => e.id === tramite.id)
        dispatch(tramiteSetActive(tramiteFull[0]))
        dispatch(uiOpenTramiteModal())
    }
    const handleDelete = (tramite:any) => {
        Swal.fire({
            title:'Are you sure?',
            text:'You wont be able to revert this!',
            icon:'warning',
            showCancelButton: true
        }).then((result)=>{
            if(result.isConfirmed){
                dispatch(tramiteStartDelete(tramite.id))
            }
        })
    }
    const handleDone = (tramite:any) => {
        const tramiteFull = tramites.filter(e => e.id === tramite.id)
        tramiteFull[0].isComplete = !tramiteFull[0].isComplete
        dispatch(tramiteStartUpdate(tramiteFull[0]))
    }
    return (
        <div className="tms__list__container">
            <MaterialTable 
                columns={columns}
                data={tramiteParsed}
                title=""
                actions={[
                    {
                        icon:'check',
                        tooltip:'Terminado',
                        onClick: (event, rowData) => handleDone(rowData)
                    },
                    {
                        icon:'edit',
                        tooltip:'Editar',
                        onClick: (event, rowData) => handleEdit(rowData) 
                    },
                    {
                        icon:'delete',
                        tooltip:'Eliminar',
                        onClick: (event, rowData) => handleDelete(rowData)
                    },
                ]}
                options={
                    {actionsColumnIndex: -1, columnsButton:true, searchFieldAlignment:'left'}
                }
            />
            
        </div>
    )
}

export default List
